.navbar {
    background: white;
    height: 70px;
    display: flex;
    justify-content:space-between;
    align-items:center;
    overflow: hidden;
    
  }
 
  
  .navbar-logo {
    
    justify-self:initial;
    cursor: pointer;
    justify-content:start;
    transform: translate(30%, 30%);
  }
  
  .fa-firstdraft {
    margin-left: 2px;
    font-size: 1.6rem;
  }
  
  .nav-menu {
    display: grid;
    height: auto;
    grid-template-columns: repeat(6, auto);
    grid-gap: 25px;
    list-style: none;
    text-align: center;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 70px;
  }
  
  .nav-links {
    color: rgb(27, 26, 26);
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-weight: bolder;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
  }
  
  .nav-links:hover {
    background-color: #4c8bca;
    border-radius: 4px;
    transition: all 0.2s ease-out;
    color:rgb(253, 250, 250);
  }
  
  .fa-bars {
    color: rgb(73, 64, 64);
  }
  
  .nav-links-mobile {
    display: none;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .navbar {
      background: white;
      height: 70px;
      display: flex;
      justify-content:space-between;
      align-items:center;
      overflow: hidden;
      
    }
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      justify-content:stretch;
      width: 100%;
      height: 480px;
      position: absolute;
      top: 70px;
      opacity: 1;
      transition: all 0.5s ease;
     padding-top: 6%;
    }
  
    .nav-menu.active {
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
      color: black;
      background-color: white;
    }
  
    .nav-links:hover {
      background-color: #4d87df;
      border-radius: 0;
      color: white;
    }
  
    .navbar-logo {
      position: relative;
      transform: translate(10%, 5%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: rgb(17, 17, 17);
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      padding: 1.5rem;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      background: #1888ff;
      text-decoration: none;
      color: #fff;
      font-size: 1.5rem;
    }
  
    .nav-links-mobile:hover {
      background: #fff;
      color: #7ef1f1;
      transition: 250ms;
    }
  
   
  }
